angular
  .module('twitterDemo', ['ui.router']);

  angular.module('twitterDemo').run(function($rootScope, $templateCache) {
     $rootScope.$on('$viewContentLoaded', function() {
        $templateCache.removeAll();
     });
  });

angular.module('twitterDemo')
.controller('HeaderController', HeaderController);

function HeaderController(){
  var vm = this;

  this.title = "Sampler!";

};

angular.module('twitterDemo')
.component('appHeader', {

  templateUrl: 'app/header/header_template.html',
  controller: 'HeaderController'

});

angular.module('twitterDemo').component('app', {
  templateUrl: 'app/app_template.html',
  controller: 'AppController'
});

angular.module('twitterDemo').controller('AppController', [function AppController(){
  var vm = this;
  vm.name = "Mika";

}]);

angular.module("twitterDemo").run(["$templateCache", function($templateCache) {$templateCache.put("app/app_template.html","<app-header></app-header>\r\n<p> Hello {{$ctrl.name}}! </p>\r\n");
$templateCache.put("app/header/header_template.html","<div class=\"header-container\">\r\n  <div class=\"button-container right-separator\">\r\n      <div class=\"button button--rounded button--active\">\r\n        <i class=\"fa fa-envira\" aria-hidden=\"true\"></i>\r\n      </div>\r\n  </div>\r\n  <div class=\"flex layout-row main-menu--container\">\r\n      <div class=\"button-container\">\r\n        <div class=\"button button--rounded\">\r\n          <i class=\"fa fa-bell\" aria-hidden=\"true\"></i>\r\n        </div>\r\n      </div>\r\n      <div class=\"button-container\">\r\n        <div class=\"button button--rounded\">\r\n          <i class=\"fa fa-envelope\" aria-hidden=\"true\"></i>\r\n        </div>\r\n      </div>\r\n      <div class=\"button-container menu--active\">\r\n        <div class=\"button button--rounded\">\r\n          <i class=\"fa fa-hashtag\" aria-hidden=\"true\"></i>\r\n        </div>\r\n      </div>\r\n  </div>\r\n  <div class=\"flex flex-rem layout-row layout-align-center-center\">\r\n    <div class=\"logo\"></div>\r\n  </div>\r\n  <div class=\"flex layout-align-end-center\">\r\n      <div class=\"input--search-container\">\r\n        <input type=\"text\" placeholder=\"Search\" class=\"input--search\">\r\n      </div>\r\n      <div class=\"margin-lr-20 button button--rounded profile--img\"></div>\r\n  </div>\r\n</div>\r\n");}]);
angular
  .module('twitterDemo')
  .config(routesConfig);

/** @ngInject */
function routesConfig($stateProvider, $urlRouterProvider, $locationProvider) {
  $locationProvider.html5Mode(true).hashPrefix('!');
  $urlRouterProvider.otherwise('/');

  $stateProvider
    .state('app', {
      url: '/',
      component: 'app'
    });
}
